'use strict';
document.addEventListener('DOMContentLoaded', function () {
    inicioApp();
});

function inicioApp() {
    navegacionFija();
    scrollArriba();
    ajuste();
    mostrarBtnScroll();
}

//Adición de etiquetas para los media queries
function ajuste() {
    const mediaQueryTablet = window.matchMedia('(min-width: 768px)');
    const mediaQueryDesktop = window.matchMedia('(min-width: 1200px)');

    function anadirEtiquetaTablet() {
        const h3Element = document.querySelector('.h3');
        if (h3Element) {
            h3Element.classList.add('menosMargenBottom');
        }
    }

    function removeEtiquetaTablet() {
        const h3Element = document.querySelector('.h3');
        if (h3Element) {
            h3Element.classList.remove('menosMargenBottom');
        }
    }

    if (mediaQueryTablet.matches) {
        anadirEtiquetaTablet();
    } else {
        removeEtiquetaTablet();
    }

    if (mediaQueryDesktop.matches) {
        removeEtiquetaTablet();
    }
}

// Asignación de evento resize a la función ajuste()
window.addEventListener('resize', ajuste);

//Fijar navegación
function navegacionFija() {
    const barra = document.querySelector('.header');
    const scServicios = document.querySelector('.inicio-bloqueo-barra');
    const body = document.querySelector('body');

    window.addEventListener('scroll', function () {

        if (scServicios.getBoundingClientRect().top < 0) {
            barra.classList.add('fijo');
            body.classList.add('body-scroll');
        } else {
            barra.classList.remove('fijo');
            body.classList.remove('body-scroll');
        }
    });
}

//Botón para hacer scroll hacía arriba
const botonRegresar = document.querySelector('.scrollTop');

// Ocultar botón al cargar la página

botonRegresar.style.display = 'none';

//Scroll
function scrollArriba() {
    botonRegresar.addEventListener('click', function () {
        window.scrollTo({
            top: 0,
            behavior: 'smooth'
        });
    });
}

//Aparecer y desaparecer botón de scroll

function mostrarBtnScroll() {
    const posicionScroll = window.scrollY;

    if (posicionScroll > 200) {
        botonRegresar.classList.add('active');
        botonRegresar.style.display = 'block';
        botonRegresar.classList.remove('hide');

    } else {
        botonRegresar.classList.remove('active');
        botonRegresar.style.display = 'none';
        botonRegresar.classList.add('hide');
    }
}

//Escuchar el evento de scroll
window.addEventListener('scroll', mostrarBtnScroll);

